<template>
  <div id="formulario-persona">
    <form v-on:submit.prevent="enviarFormulario">
      <div class="container">
        <div class="row">
          <div class="col-md-4">
            <div class="form-group">
              <label for="name">Nombre</label>
              <input type="text" name="name" id="name" class="form-control"
              ref="nombre" v-model="persona.nombre" :class="{ 'is-invalid': procesando && nombreInvalido }" v-on:focus="resetEstado" v-on:keypress="resetEstado">
            </div>
          </div>
          <div class="col-md-4">
            <div class="form-group">
              <label for="surname">Apellido</label>
              <input type="text" name="surname" id="surname" class="form-control"
              ref="apellido" v-model="persona.apellido" :class="{ 'is-invalid': procesando && apellidoInvalido }" v-on:focus="resetEstado" v-on:keypress="resetEstado">
            </div>
          </div>
          <div class="col-md-4">
            <div class="form-group">
              <label for="email">Email</label>
              <input type="text" name="email" id="email" class="form-control"
              ref="email" v-model="persona.email" :class="{ 'is-invalid': procesando && emailInvalido }" v-on:focus="resetEstado" v-on:keypress="resetEstado">
            </div>
          </div>
        </div>
        <div class="row">
          <div class="col-md-4">
            <div class="form-group">
              <button type="submit" class="btn btn-primary">A√±adir persona</button> 
            </div>
          </div>
        </div>
      </div>
      <div class="container">
        <div class="row">
          <div class="col-md-12">
            <div v-if="error && procesando" class="alert alert-danger" role="alert">
              Debes rellenar todos los campos!
            </div>
            <div v-if="correcto" class="alert alert-success" role="alert">
              La persona ha sido agregada correctamente!
            </div>
          </div>
        </div>
      </div>
    </form>
  </div>
</template>

<style>
  form{
    margin-bottom: 2rem;
  }
</style>

<script>
  export default {
    name: 'FormularioPersona',
    data() {
      return {
        procesando: false,
        correcto: false,
        error: false,
        persona: {
          nombre: '',
          apellido: '',
          email: '',
        },
      }
    },
    methods: {
      enviarFormulario() {
        this.procesando = true;
        this.resetEstado();
        
        // Comprobamos la presencia de errores
        if (this.nombreInvalido || this.apellidoInvalido || this.emailInvalido) {
          this.error = true;
          return;
        }
        
        this.$emit('add-persona', this.persona);
        this.$refs.nombre.focus();
        this.$refs.apellido.focus();
        this.$refs.email.focus();

        this.error = false;
        this.correcto = true;
        this.procesando = false;

        // Restablecemos el valor de la variables
        this.persona = {
          nombre: '',
          apellido: '',
          email: '',
        }
      },
      resetEstado() {
        this.correcto = false;
        this.error = false;
      },
    },
    computed: {
      nombreInvalido(){
        return this.persona.nombre.length < 1;
      },
      apellidoInvalido(){
        return this.persona.apellido.length < 1;
      },
      emailInvalido(){
        return this.persona.email.length < 1;
      },
    }
  }
</script>